/* src/styles/Sidebar.css */

.sidebar {
  width: 280px;
  height: 100vh;
  background-color: #111111;
  color: white;
  padding: 2rem 1.5rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1000;
  overflow-y: auto;
  border-right: 2px solid #1f1f1f;
  box-shadow: 2px 0 10px rgba(255, 215, 0, 0.1);
}

/* Scrollbar */
.sidebar::-webkit-scrollbar {
  width: 6px;
}
.sidebar::-webkit-scrollbar-thumb {
  background-color: #444;
  border-radius: 3px;
}

/* Profile Image */
.profile-pic {
  width: 120px;
  height: 120px;
  object-fit: cover;
  border-radius: 50%;
  border: 2px solid gold;
  margin-bottom: 1.5rem;
  transition: transform 0.3s ease;
}
.profile-pic:hover {
  transform: scale(1.08);
  box-shadow: 0 0 15px gold;
}

/* Text & Typography */
h1 {
  font-size: 1.8rem;
  font-weight: bold;
  margin-bottom: 0.3rem;
  text-align: center;
}

.title {
  font-size: 1.1rem;
  font-weight: 500;
  color: #cccccc;
  margin-bottom: 0.5rem;
  text-align: center;
}
.subtitle {
  font-size: 1rem;
  color: #aaaaaa;
  text-align: center;
  margin-bottom: 1.5rem;
}

.divider {
  width: 80%;
  height: 1px;
  background-color: #444;
  margin: 1.5rem 0;
}

/* Contact Info */
.contact-info {
  width: 100%;
  font-size: 0.95rem;
  line-height: 1.8;
}

.contact-item,
.contact-link {
  display: flex;
  align-items: center;
  gap: 8px;
  margin-bottom: 0.8rem;
  color: #ccc;
  text-decoration: none;
  font-size: 0.95rem;
}

.contact-link:hover {
  color: #1da1f2;
}

/* Social Icons */
.social-footer {
  display: flex;
  justify-content: center;
  gap: 20px;
  margin-top: 1rem;
}
.social-icon {
  font-size: 1.5rem;
  color: #0077ff;
  transition: transform 0.3s ease, color 0.3s ease;
}
.social-icon:hover {
  color: gold;
  transform: scale(1.3);
}

/* Responsive: Make sidebar collapsible */
@media screen and (max-width: 768px) {
  .sidebar {
    position: relative;
    width: 100%;
    height: auto;
    padding: 2rem 1rem;
    border-right: none;
    border-bottom: 2px solid #1f1f1f;
  }
}
