/* Global box sizing */
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

/* Layout container */
.app-container {
  display: flex;
  min-height: 100vh;
  font-family: 'Segoe UI', sans-serif;
  background-color: #111;
  color: #fff;
  overflow: hidden;
}

/* Sidebar styles */
.sidebar {
  width: 280px;
  min-width: 280px;
  background-color: #111;
  color: #fff;
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  z-index: 1000;
  padding: 1.5rem 1rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  overflow-y: auto;
  border-right: 1px solid #333;
}

/* Right panel (navbar + main content) */
.right-panel {
  margin-left: 280px;
  width: calc(100% - 280px);
  display: flex;
  flex-direction: column;
  height: 100vh;
  overflow: hidden;
}

/* Navbar stays fixed inside right panel */
.navbar {
  width: 100%;
  background-color: #111;
  padding: 12px 24px;
  display: flex;
  justify-content: flex-end;
  position: sticky;
  top: 0;
  z-index: 999;
  box-shadow: 0 1px 4px rgba(0, 255, 255, 0.1);
}

/* Main content (scrollable area) */
.main-content {
  flex: 1;
  overflow-y: auto;
  padding: 2rem;
  background-color: #111;
}

/* Section styling */
.section {
  margin-bottom: 3rem;
}

/* Headings and links */
h1, h2, h3 {
  color: cyan;
}

a {
  text-decoration: none;
  color: cyan;
}

a:hover {
  color: #00ffee;
}

/* Responsive layout for mobile */
@media (max-width: 768px) {
  .app-container {
    flex-direction: column;
  }

  .sidebar {
    width: 100%;
    position: relative;
    min-width: 100%;
    height: auto;
    padding: 1rem;
  }

  .right-panel {
    margin-left: 0;
    width: 100%;
    height: auto;
  }

  .main-content {
    padding: 1rem;
  }
}
