/* src/styles/Navbar.css */

.navbar {
  width: 100%;
  background-color: #111;  /* transparent; */
  padding: 12px 24px;
  display: flex;
  justify-content: flex-end;
  position: sticky;
  top: 0;
  z-index: 1000;
  box-shadow: 0 1px 4px rgba(0, 255, 255, 0.1);
}

.nav-list {
  display: flex;
  list-style: none;
  gap: 20px;
  flex-wrap: wrap;
}

.nav-item {
  color: #ccc;
  cursor: pointer;
  font-weight: 500;
  transition: color 0.3s, border 0.3s;
}

.nav-item:hover,
.nav-item.active {
  color: #00ffff;
  border-bottom: 2px solid #00ffff;
}

/* Mobile fix */
@media (max-width: 768px) {
  .navbar {
    justify-content: center;
    padding: 12px;
  }

  .nav-list {
    gap: 12px;
    flex-wrap: wrap;
    justify-content: center;
  }
}
